import tabBar from '../components/tabBar'
export default{
	data(){
		return{
			msg:'',
			hasSetBg:false
		}
	},
	components:{
		tabBar
	},
	onLoad(){
		this.setBg();
	},
	methods:{
		getPhoneNumber(){
			
		},
		toLogin(){   // 登录
			uni.navigateTo({
				url:'../API/login/login'
			})
		},
		toGetUserInfo(){  // 获取用户信息
			uni.navigateTo({
				url:'../API/get-user-info/get-user-info'
			})
		},
		toGetLocation(){
			uni.navigateTo({
				url:'../API/choose-location/choose-location'
			})
		},
		setBg() {
			this.hasSetBg = !this.hasSetBg;
			uni.setNavigationBarColor({
				frontColor: this.hasSetBg ? "#ffffff" : "#000000",
				backgroundColor: this.hasSetBg ? "#007AFF" : "#F8F8F8"
			})
		},
		toGetPhone(){
			console.log('要去获取用户的手机号');
			uni.navigateTo({
				url:'../API/get-user-info/get-user-info'
			})
		},
		toClickMenu(){  // 去点餐
			console.log('去点餐');
			uni.navigateTo({
				url:'../shopMall/index'
			})
		},
		toPay(){  // 去买单
		console.log('去买单');
			uni.navigateTo({
				url:'../API/request-payment/request-payment'
			})
		},
		toMy(){
			uni.navigateTo({
				url:'../my/index'
			})
		},
		toHome(){
			uni.navigateTo({
				url:'../home/home'
			})
		},
		toAllOrder(key){
			uni.navigateTo({
				url:'./orderList?id='+key
			})
		},
		toShare(){
			uni.share({
				provider: "weixin",
				scene: "WXSceneSession",
				type: 1,
				summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
				success: function (res) {
					console.log("success:" + JSON.stringify(res));
				},
				fail: function (err) {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		},
		toGifts(){  // 赠品
			uni.navigateTo({
				url:'./gifts'
			})
		},
		toScore(){  // 积分
			uni.navigateTo({
				url:'./score'
			})
		},
		toMoney(){   // 账户余额
			uni.navigateTo({
				url:'./money'
			})
		},
		toCoupon(){
			uni.navigateTo({
				url:'./coupon'
			})
		},
		toAddress(){
			uni.navigateTo({
				url:'./address'
			})
		}
	}
}
